import "./ListItem.css"
import { useState, useRef, useEffect } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { 
    // faEllipsisV,
    faChevronCircleRight,
} from '@fortawesome/free-solid-svg-icons'

export default function ListItem(props) {
    const MAXLENGTH = 25
    const itemName = props.item.name
    const toDo = props.item.toDo

    const [counter, setCounter] = useState(0)
    const [highlight, setHighlight] = useState(false)
    const intervalRef = useRef(null)

    useEffect(() => {
        return () => stopCounter()
    }, [])

    useEffect(() => {
        if (counter > 50) {
            // console.log("hit it")
            setHighlight(prev => !prev)
            setCounter(0)
            stopCounter()
        }
    }, [counter])

    function startCounter() {
        if (intervalRef.current) return
        intervalRef.current = setInterval(() => {
            setCounter((prevCounter) => prevCounter + 1)
        }, 10)
    }

    function stopCounter() {
        if (intervalRef.current) {
            clearInterval(intervalRef.current)
            intervalRef.current = null
        }
    }

    // console.log(counter)
    
    return (
        <div 
            className="container" 
            style={{
                // background: props.selected && "lightgray"
                background: highlight && "lightgray"
            }} 
            onMouseDown={startCounter}
            onMouseUp={stopCounter}
            onMouseLeave={stopCounter}
        >
            <div className="clickArea" onClick={() => props.handleChange(toDo, props.id)}>
                <input 
                    type="checkbox" 
                    checked={!toDo}
                    value={!toDo}
                    onChange={props.handleChange}
                    id={props.id}
                />
                <span className="checkmark"></span>
            </div>
            <div className="itemName">
                {itemName.length < MAXLENGTH ? 
                    itemName :
                    itemName.slice(0,MAXLENGTH) + "..."
                }
            </div>
            <div className="iconArea" onClick={() => props.menuClick(props.id)}>
                {props.icon ? 
                    <FontAwesomeIcon icon={props.icon} /> :
                    <FontAwesomeIcon icon={faChevronCircleRight} />
                }
            </div>
        </div>
    )
}